@import url(https://fonts.googleapis.com/css?family=ABeeZee:400,400italic);



html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */

body{
  box-sizing: border-box;
  margin: 0 auto;
}

.wrapper{
	max-width: 1560px;
	position: relative;
	}
section{
	height: auto;
}
section img:first-child{
	height: auto;
}

section>img:nth-child(3){

}
section>div:nth-child(2){
	position: absolute;
	top: 32px;
	margin-left: 10%;
	width: 7%;
	float: left;
	display: inline-block;
}

section>div:nth-child(3){
	position: absolute;
	top: 47px;
	margin-left: 25%;
	width: 49%;
	display: inline-block;
}

section>div:nth-child(3) ul li{
	display: inline-block;
	margin-right: 5.5%;
}

section>div:nth-child(3) ul li a{
	font-size: 1.2rem;
	color: #887d7d;
	font-weight: bold;
}

section>div:nth-child(4){
position: absolute;
top: 25px;
margin-left: 78%;
}
section>div:nth-child(4) span{
	display: block;
	font-size: 1.0rem;
	color: #887d7d;
	font-weight: bold;
}

section>div:nth-child(4)>span:last-child{
	margin-left: -26px;
	margin-top: 7px;
}

.menuToggle{
	 display: none;
}
section+div{
	position: relative;
	margin: 0 auto;
	width: 300px;
	color: #863048;
	margin-top: -26%;
	font-size: 2rem;
}

section+div>p{
display: inline-block;
border-bottom: 1px solid #af7988;
height: 27px;
font-size: 2rem;
color: #863048;
}

section+div+div{margin-top: 2%;padding: 0 23%;font-size: 0.95rem;text-align: center;margin-bottom: 4%;color: #8e8684;}

section+div+div p{display: block;margin: 0 18%;text-align: center;}

.foto_row{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-wrap:wrap;
	justify-content: space-between;
}
.foto_row img{
	margin: auto;
}
.col{
	height: 150px;
}

aside{
-webkit-box-sizing: border-box;
-moz-box-sizing: border-box;
box-sizing: border-box;
margin-top: 100px;
}
aside h3{
	margin: 25px auto;
	color:#863048;
	width: 115px;
	border-bottom: 1px solid #863048;
	height: 45px;
}

aside>div:first-child{
	padding-left: 5%;
	width: 45%;
	text-align: justify;
	display: inline-block;

}

aside>div:last-child{
	padding-left: 5%;
	width: 50%;
	height: 272px;
	position: relative;
	display: inline-block;

}


aside>div:last-child img{
	position: absolute;
}

aside>div:last-child>img:nth-child(1){
	margin-top: 265px;
    margin-left: 375px;


}
aside>div:last-child>img:nth-child(2){
	margin-top: 335px;
    margin-left: 166px;


}
aside>div:last-child>img:nth-child(3){
	margin-top: 188px;
    margin-left: 23px;
    height: 177px;

}
aside>div:last-child>img:nth-child(4){

margin-left: 125px;
}
aside + div{
	margin: 300px auto 40px;
    display: block;
    color: #863048;
    width: 300px;
    font-size: 2rem;
}

aside+ div>p{
	display: inline-block;
    border-bottom: 1px solid #af7988;
    height: 27px;
    color: #863048;
}
.reviews+div+div{
color: #863048;
margin: 2% auto;
    width: 213px;
    display: block;
    font-size: 2rem;
}

.reviews+div+div p{
border-bottom: 1px solid #af7988;
    height: 27px;
    color: #863048;
display: inline-block;
    margin: 0 8px 0;

}
.reviews div{
	display: inline-block;
	width: 45%;
	position: relative;
	padding: 0 8%;

}

.reviews{
	width: 100%;
}
.reviews+div{
	margin-bottom: 370px;
}
.reviews+div img{
float: right;

}

.reviews div img{
	position: absolute;
	margin: -56px 0 0 -71px;
}
.reviews div h4{
	margin: 0 auto;
	width: 38%;
	color: #863048;
}
.reviews div p{
color: #121212;
text-align: justify;
}
.foto>div:last-child{

	width: 431px;
}

.foto>div:first-child{

	width: 421px;
}
.foto>div:nth-child(2){

	width: 244px;
}
.foto{
	display: block;
}



.foto + div{
	margin: 50px auto 40px;
    display: block;
    color: #863048;
    width: 300px;
    font-size: 2rem;
}
.foto+ div>p{
	display: inline-block;
    border-bottom: 3px solid #af7988;
    height: 40px;
    color: #863048;
}

.footer{
	position: relative;
	max-width: 1560px;
}

.footer>img:nth-child(2){

    margin: -103px 97px;
    position: absolute;

}
.footer>div i{
	margin-right: 50px;
}

.footer>div{
margin-top: -112px;
padding-left: 70%;
color:#fff;

}
.footer>div:nth-child(4){
	padding: 0;
    margin-top: 30px;
    margin-left: 40%;

}

.foto+div+div{

}

.primerka img{
display: inline-block;
width: 60%;
}

.forma{
    width: 40%;
    height: 300px;
 	float: right;
    background: #d6d3c7;
}

.forma input{
	/*border-bottom-color: #863048;*/
	text-align: center;
    -webkit-text-fill-color: #863048;
    border:none;
}

/*=====MEDIA=========*/
