@import "smart-grid.sass";
@import "main.scss";

.mol {
	@include row-flex();
	@include justify-content(center);
	@include align-items(stretch);
	@include align-content(stretch);
	background: #fff;
	height: 300px;
	border: 1px solid red; }

.col1 {
	border: 1px solid red;
	background: #454545;
	@include col();
	@include col-3();
	@include col-lg-3(); }

.col {
	border: 1px solid red;
	@include col();
	@include col-1();
	@include col-lg-3();
	@include col-md-4();
	@include col-sm-6();
	@include col-xs-12();
	background: #454545;
	@include align-self(center); }

.forma {
	@include row-flex;
	@include justify-content(center);
	@include align-items(space-between);
	@include align-content(space-around); }

.forma input {
	@include col();
	@include col-6(); }


.forma input:last-child {
	@include col();
	@include col-12(); }

.primerka {
	@include row-flex;
	@include justify-content(center);
	@include align-items(center); }



.foto {
	@include row-flex;
	@include justify-content(center); }


